article {
  margin: 0;
}
footer {
  padding: 5vh 0;
  margin-top: 10vh;
  background-color: #333333;
  color: #959595;
}
.footer_wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 80%;
  font-size: 0.8em;
}
.footer_right p {
  text-transform: lowercase;
}
.arrow_top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  background-color: #9595957d;
  text-align: center;
  line-height: 55px;
  border-radius: 5px;
  z-index: 9999;
}
.arrow_top a span {
  font-size: 1.5em;
  color: white;
}
