html {
  scroll-behavior: smooth;
}
nav {
  /* flex-container */
  text-transform: uppercase;
  background-color: #333333;
  display: flex;
  justify-content: space-around;
  /* align-items:  */

  position: fixed;
  width: 100%;
  height: 80px;
  z-index: 9999;
}
nav h1 {
  /* flex-item */
  /* margin-right: auto; 플렉스 아이템 개별 정렬 */
  margin-left: 3vw;
  position: relative;
  top: -15px;
  img {
    height: 80px;
  }
  /* justify-content: flex-start; */
}
// nav h1 a img {
